ThreejsAPI.prototype.cheshenAdd = function () {

    return new Promise((resolve) => {
        gltfLoader.load('./static/model/car/cheshenD.glb', function (gltf) {
            gltf.scene.name = 'cheshen'
            gltf.scene.visible=false
            gltf.scene.children.forEach(function (child) {
                if (child.isMesh) {
                    child.material.envMap = HallCube
                    if (child.name==='jinshuyansecheshen'||child.name==='jinshuyansecheshen2'||child.name==='jinshuyansehoushijingzhao'||child.name==='jinshubashou') {
                        child.material.color=new THREE.Color(0xffffff)
                        child.material.envMap = HallCube
                        child.material.reflectivity=0.2
                        child.material.roughness=2.0
                        child.material.envMapIntensity=0.6
                    }
                }
            })
            scene.add(gltf.scene)
            resolve()
            // gltf.scene.visible = true
        })
    })
}

ThreejsAPI.prototype.carOthersAdd = function () {

    return new Promise((resolve) => {
        gltfLoader.load('./static/model/car/othersD.glb', function (gltf) {

            const bodyMaterial3 = new THREE.MeshBasicMaterial( {
                color: 0x000000, 
                metalness: 100.5, 
                roughness: 100.0, 
                clearcoat: 0.05, 
                clearcoatRoughness: 0.05,
                envMap : HallCube,
                reflectivity:0.3,
                shininess:87,
                combine: THREE.MixOperation,
            } );
        
            const GlassMaterial = new THREE.MeshPhysicalMaterial( {
                color: 0xffffff, metalness: 0, roughness: 0.1, transmission: 0.9, transparent: true,
                envMap: HallCube,
            } );
        

            gltf.scene.name = 'others'
            gltf.scene.visible=false
            gltf.scene.children[0].children.forEach(function (child) {

                if(child.name==='che_03polySurface15'|| child.name==='che_03polySurface15_1'){
                    child.material=GlassMaterial
                }
                else if(child.name.indexOf('bolichedengzhao')>=0){
                    child.material=GlassMaterial
                }
                else if(child.name==='jinshuwuyongtuo'){
                    child.material=bodyMaterial3
                }
                else{
                    if (child.isMesh) {
                        child.material.envMap = HallCube
                        
                    }
                }
            })
            scene.add(gltf.scene)
            resolve()
            // gltf.scene.visible = true
        })
    })
}

