ThreejsAPI.prototype.SpriteName=function(name,blnShow) {
    strName=name
    scene.children.forEach(child => {
        if (child.ctype==="css2dType") {
            if (child.name === strName) {
                child.visible = blnShow
            }
            else{
                // console.log(child.name)
                child.visible = false
                child.pointClick.innerHTML = ``;
            }
        }

    })
}


ThreejsAPI.prototype.controlChange=function(){


    var startbln = false;
    const tempV = new THREE.Vector3();
    const cameraToPoint = new THREE.Vector3();
    const cameraPosition = new THREE.Vector3();
    const normalMatrix = new THREE.Matrix3();
    controls.addEventListener('start', () => {
        startbln = true
    })
    controls.addEventListener('end', () => {
        startbln = false
    })


    controls.addEventListener('change',()=> {
        changeevent()
    })

    function changeevent(){
        if (startbln) {
            scene.children.forEach(child => {
                if (child.ctype==="css2dType") {
                    if (child.name === strName) {
                        // get a matrix that represents a relative orientation of the camera
                        normalMatrix.getNormalMatrix(camera.matrixWorldInverse);
                        // get the camera's position
                        camera.getWorldPosition(cameraPosition);
                        tempV.copy(child.position);
                        tempV.applyMatrix3(normalMatrix);
                        cameraToPoint.copy(child.position);
                        cameraToPoint.applyMatrix4(camera.matrixWorldInverse).normalize();
                        const dot = tempV.dot(cameraToPoint);
    
                        // if the orientation is not facing us hide it.
                        if (dot < child.minVisibleDot) {
                            child.visible = true
                            //   elem.style.display = 'none';
                        } else {
                            child.visible = false
                            child.pointClick.innerHTML = ``;
                        }
                    }
                    else{
                        // console.log(child.name)
                        child.visible = false
                        child.pointClick.innerHTML = ``;
                    }
                }

            })
        }


    }




}






