        '@keyframes myfirst2 {',
        '    0% {',
        '        opacity: 0;',
        '        transform: translateX(-20%);',
        '    }	',
        '    100% {',
        '        opacity: 1;',
        '        transform: translateX(0);',
        '    }',
        '} ',
        '@-moz-keyframes myfirst2', /* Firefox */
        '{',
        '    0% {',
        '        opacity: 0;',
        '        transform: translateX(-20%);',
        '    }	',
        '    100% {',
        '        opacity: 1;',
        '        transform: translateX(0);',
