function flexible(win, lib) {
    // var vConsole = new VConsole();
    var doc = win.document;
    var docEl = doc.documentElement;
    var metaEl = doc.querySelector('meta[name="viewport"]');
    var flexibleEl = doc.querySelector('meta[name="flexible"]');
    var dpr = 0;
    var scale = 0;
    var tid;
    var flexible = lib.flexible || (lib.flexible = {});
  
  
    if (metaEl) {
      var match = metaEl.getAttribute('content').match(/initial\-scale=([\d\.]+)/);
      if (match) {
        scale = parseFloat(match[1]);
        dpr = parseInt(1 / scale);
      }
    } else if (flexibleEl) {
      var content = flexibleEl.getAttribute('content');
      if (content) {
        var initialDpr = content.match(/initial\-dpr=([\d\.]+)/);
        var maximumDpr = content.match(/maximum\-dpr=([\d\.]+)/);
        if (initialDpr) {
          dpr = parseFloat(initialDpr[1]);
          scale = parseFloat((1 / dpr).toFixed(2));
        }
        if (maximumDpr) {
          dpr = parseFloat(maximumDpr[1]);
          scale = parseFloat((1 / dpr).toFixed(2));
        }
      }
    }
  
  
  
    if (!dpr && !scale) {
  
      var isAndroid = win.navigator.appVersion.match(/android/gi);
      var isIPhone = win.navigator.appVersion.match(/iphone/gi);
      var devicePixelRatio = win.devicePixelRatio;
      if (isIPhone) {
        // iOS下，对于2和3的屏，用2倍的方案，其余的用1倍方案
        if (devicePixelRatio >= 3 && (!dpr || dpr >= 3)) {
          dpr = 3;
        } else if (devicePixelRatio >= 2 && (!dpr || dpr >= 2)) {
          dpr = 2;
        } else {
          dpr = 1;
        }
      } else {
        if (isWeixin) {
          dpr = 2
        } else {
          dpr = 1; // 其他设备下，仍旧使用1倍的方案
        }
      }
      scale = 1 / dpr;
    }
  
    docEl.setAttribute('data-dpr', dpr);
    if (!metaEl) {
      metaEl = doc.createElement('meta');
      metaEl.setAttribute('name', 'viewport');
      metaEl.setAttribute('content', 'initial-scale=' + scale + ', maximum-scale=' + scale + ', minimum-scale=' + scale + ', user-scalable=no');
      if (docEl.firstElementChild) {
        docEl.firstElementChild.appendChild(metaEl);
      } else {
        var wrap = doc.createElement('div');
        wrap.appendChild(metaEl);
        doc.write(wrap.innerHTML);
      }
    }
  
    var ua = win.navigator.userAgent.toLowerCase();
    var isWeixin = ua.indexOf('micromessenger') != -1;
    console.log(isWeixin)
    //这个是750px的效果图 /100就能算出rem
    function refreshRem() {
      var width = docEl.getBoundingClientRect().width
      console.log(width)
      // 适配平板
      if (width / dpr > 750) {
        width = 750 * dpr
      }
      var oldSize = 100 * (width / 750) //oldside
      docEl.style.fontSize = oldSize + 'px'
      flexible.rem = win.rem = oldSize;
  
      if (isWeixin) {
        // var nowSize = window.getComputedStyle(doc.getElementsByTagName("html")[0]).fontSize;
        // var oldSizeValue = parseFloat(oldSize, 10);
        // var nowSizeValue = parseFloat(nowSize, 10);
        // console.log((nowSizeValue - oldSizeValue))
        // if ((nowSizeValue - oldSizeValue) > 1 || (nowSizeValue - oldSizeValue) < -1) {
        //   console.log(diff)
        //   var diff = (oldSizeValue / nowSizeValue);
        //   docEl.style.fontSize = (100 * (width / 750) * diff) + 'px';
        //   flexible.rem = win.rem = (100 * (width / 750) * diff) + 'px';
        // }
        var html = document.getElementsByTagName('html')[0];
        var settingFs
        var settedFs = settingFs = parseFloat(html.style.fontSize).toFixed(4);
        var whileCount = 0;
        while (true) {
          var realFs = parseFloat(window.getComputedStyle(html).fontSize).toFixed(4);
          console.log("device html fontSize " + settedFs + " actual rem unit " + realFs);
          var delta = realFs - settedFs;
          if (Math.abs(delta) >= 1) //不相等
          {
            /*if (delta > 0) settingFs--; else settingFs++;*/
            var lastFontSize = settingFs * settingFs / realFs;
            console.log("calc html fontsize " + lastFontSize);
            html.setAttribute('style', 'font-size:' + lastFontSize.toFixed(4) + 'px!important');
          } else{
            break;
          }
          if (whileCount++ > 10){
            break
          } //如果是比例缩放的话,while最多2次，如果不是这方法就有点凉凉了，所以最好有个出口
            
        }
  
  
      }
  
    }
  
    win.addEventListener('resize', function () {
      clearTimeout(tid);
      tid = setTimeout(refreshRem, 300);
    }, false);
    win.addEventListener('pageshow', function (e) {
      if (e.persisted) {
        clearTimeout(tid);
        tid = setTimeout(refreshRem, 300);
      }
    }, false);
  
    if (doc.readyState === 'complete') {
      doc.body.style.fontSize = 12 * dpr + 'px';
    } else {
      doc.addEventListener('DOMContentLoaded', function (e) {
        doc.body.style.fontSize = 12 * dpr + 'px';
      }, false);
    }
  
  
    refreshRem();
  
    flexible.dpr = win.dpr = dpr;
    flexible.refreshRem = refreshRem;
    flexible.rem2px = function (d) {
      var val = parseFloat(d) * this.rem;
      if (typeof d === 'string' && d.match(/rem$/)) {
        val += 'px';
      }
      return val;
    }
    flexible.px2rem = function (d) {
      var val = parseFloat(d) / this.rem;
      if (typeof d === 'string' && d.match(/px$/)) {
        val += 'rem';
      }
      return val;
    }
  
  }

  export {flexible}