import React from 'react';
import './Videoplay.css';
import { Player, BigPlayButton,ControlBar } from 'video-react';
import 'video-react/dist/video-react.css'; // import css

// eslint-disable-next-line
export default class Videoplay extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            blnshow: true,
        };
        this.CloseVideo = this.CloseVideo.bind(this);


    }

    CloseVideo(event) {
        this.setState({ blnshow: false });
        event.nativeEvent.stopImmediatePropagation()
        event.stopPropagation()
    }


    



    componentWillReceiveProps(newProps) {
        this.setState({ blnshow: true });
    }
    


    render() {
        return (
            this.state.blnshow ?
                <div className="mask-layer" id="maskLayer" onClick={this.CloseVideo}>
                    <div className="video-box" id="videoBox" onClick={(e)=>e.stopPropagation()}>
                    

                    <video src={this.props.src}  autoPlay className="videowidth" webkit-playsinline="true" x-webkit-airplay="true" playsinline="true" x5-video-player-type="h5" x5-video-orientation="h5" x5-video-player-fullscreen="true" webkit-playsinline preload="auto" controlslist="nodownload" disablePictureInPicture></video>
                    </div>
                    {/* <Player
                        src={this.props.src}
                        autoPlay
                        > */}
                    {/* <BigPlayButton position="center" />
                    <ControlBar autoHide={true}  disableCompletely={true}></ControlBar>
                    </Player> */}
                    
                    <div className="close-box">
                        <img src="./images/close2.svg" onClick={this.CloseVideo} />
                    </div>
                </div>
                : null

        )
    }
}