function getDevice (win) {
    var ua = navigator.userAgent
    // 是否为移动端
    var mobile = /AppleWebKit.*Mobile.*/.test(ua)
    // ios终端
    var ios = /\(i[^;]+;(U;)?CPU.+Mac OS X/.test(ua)
    // android终端或者uc浏览器
    var android = /(Android);?[\s/]+([\d.]+)?/.test(ua)
    // iphone
    var iphone = /iphone/i.test(ua)
    // ipad
    var ipad = /iPad/i.test(ua)
    // weixin
    var weixin = /micromessenger/i.test(ua)
    // Chrome
    var chrome = /Chrome\/([\d.]+)/.test(ua) || /CriOS\/([\d.]+)/.test(ua)
    // 火狐内核
    var mozilla = ua.indexOf('Gecko') > -1 && ua.indexOf('KHTML') === -1
    // 苹果，谷歌内核
    var webkit = /AppleWebKit/i.test(ua)
    // opera内核
    var opera = /Presto/i.test(ua)
    // 苹果浏览器
    var safari = /safari/i.test(ua) && !/Chrome/i.test(ua)
    // 微软
    var msie = /msie/i.test(ua)
    // PC
    var pc = !/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i.test(
      ua
    )
  
    win.deviceMsg = {
      isMobile: mobile,
      islos: ios,
      isAndroid: android,
      isIPhone: iphone,
      isIPad: ipad,
      isWeiXin: weixin,
      isChrome: chrome,
      isMozilla: mozilla,
      isWebkit: webkit,
      isOpera: opera,
      isSafari: safari,
      isMsie: msie,
      isPc: pc
    }
  }
  
  export { getDevice }
  