import React from 'react';
import './ImageShow.css';

// eslint-disable-next-line
export default class ImageShow extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            blnshow: true,
        };
        this.CloseImage = this.CloseImage.bind(this);


    }

    CloseImage() {
        this.setState({ blnshow: false });
    }

    componentWillReceiveProps(newProps) {
        this.setState({ blnshow: true });
    }
    


    render() {
        return (
            this.state.blnshow ?
                <div className="mask-layer" id="maskLayer" onClick={this.CloseImage}>
                    <img className="img-show" src={this.props.src} style={this.props.stylewidth} onClick={(e)=>e.stopPropagation()}></img>
                    <div className="close-box">
                        <img src="./images/close2.svg" onClick={this.CloseImage} />
                    </div>
                </div>
                : null

        )
    }
}